import {
  DummyConfig,
  DummyFilehandler,
  FileHandlerInterface,
  SambaConfig,
  SambaFilehandler,
  SftpConnectConfig,
  SftpFilehandler,
  SftpReconnectFilehandler,
  SftpRegularConfig,
} from '@xip-online-data/file-system-connector';

export const fileHandlerFactory = (
  config: SftpRegularConfig | SftpConnectConfig | SambaConfig | DummyConfig,
): FileHandlerInterface => {
  switch (config.type) {
    case 'samba':
      return new SambaFilehandler(config);
    case 'sftp':
      return new SftpFilehandler(config);
    case 'sftp-reconnect':
      return new SftpReconnectFilehandler(config);
    case 'dummy':
      return new DummyFilehandler(config);
    default:
      throw new Error('Invalid file handler type');
  }
};
